<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 项目代码 -->
            <a-form-model-item
              label="项目代码"
              prop="projectCode"
              ref="projectCode"
            >
              <a-input
                v-model="entity.projectCode"
                placeholder="请输入项目代码"
                name="projectCode"
                :disabled="!!id"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 项目名称 -->
            <a-form-model-item
              label="项目名称"
              prop="projectName"
              ref="projectName"
            >
              <a-input
                v-model="entity.projectName"
                placeholder="请输入项目名称"
                name="projectName"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 适用设备类型 -->
            <a-form-model-item
              label="适用设备类型"
              prop="applicableDevices"
              ref="applicableDevices"
            >
              <a-select
                v-model="entity.applicableDevices"
                placeholder="请选择适用设备类型"
                mode="multiple"
                name="applicableDevices"
              >
                <a-select-option v-for="item in deviceTypeOptions" :key="item.value" :value="item.value">
                  {{ item.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 维修级别 -->
            <a-form-model-item
              label="维修级别"
              prop="repairLevel"
              ref="repairLevel"
            >
              <a-select
                v-model="entity.repairLevel"
                placeholder="请选择维修级别"
                name="repairLevel"
              >
                <a-select-option v-for="item in repairLevelOptions" :key="item.value" :value="item.value">
                  {{ item.label }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 平均工时 -->
            <a-form-model-item
              label="平均工时(小时)"
              prop="averageDuration"
              ref="averageDuration"
            >
              <a-input-number
                v-model="entity.averageDuration"
                placeholder="请输入平均工时"
                name="averageDuration"
                :min="0.1"
                :max="999"
                :step="0.1"
                :precision="1"
                style="width: 100%"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 所需配件 -->
            <a-form-model-item
              label="所需配件"
              prop="requiredParts"
              ref="requiredParts"
            >
              <a-textarea
                v-model="entity.requiredParts"
                placeholder="请输入所需配件，如无则填'无'"
                :rows="3"
                :maxLength="500"
                name="requiredParts"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 维修步骤 -->
            <a-form-model-item
              label="维修步骤"
              prop="repairSteps"
              ref="repairSteps"
            >
              <a-textarea
                v-model="entity.repairSteps"
                placeholder="请输入详细维修步骤，建议使用编号区分不同步骤"
                :rows="6"
                :maxLength="2000"
                name="repairSteps"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 注意事项 -->
            <a-form-model-item
              label="注意事项"
              prop="precautions"
              ref="precautions"
            >
              <a-textarea
                v-model="entity.precautions"
                placeholder="请输入维修注意事项"
                :rows="4"
                :maxLength="1000"
                name="precautions"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="详细信息" v-if="id">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditRepairProject",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        projectCode: "",
        projectName: "",
        applicableDevices: [],
        repairLevel: undefined,
        averageDuration: 1.0,
        requiredParts: "",
        repairSteps: "",
        precautions: "",
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      // 设备类型选项
      deviceTypeOptions: [
        { value: "交换机", label: "交换机" },
        { value: "路由器", label: "路由器" },
        { value: "服务器", label: "服务器" },
        { value: "存储设备", label: "存储设备" },
        { value: "防火墙", label: "防火墙" }
      ],
      // 维修级别选项
      repairLevelOptions: [
        { value: "一级", label: "一级（简单维修）" },
        { value: "二级", label: "二级（中等维修）" },
        { value: "三级", label: "三级（复杂维修）" }
      ],
      rules: {
        projectCode: [
          { required: true, message: "项目代码不能为空", trigger: "change" },
          {
            pattern: /^RP[0-9]{3}$/,
            message: "项目代码格式不正确，应为RP开头加3位数字",
            trigger: "change"
          }
        ],
        projectName: [
          { required: true, message: "项目名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "项目名称不能包含前后空格",
            trigger: "change"
          }
        ],
        applicableDevices: [
          { required: true, message: "适用设备类型不能为空", trigger: "change" }
        ],
        repairLevel: [
          { required: true, message: "维修级别不能为空", trigger: "change" }
        ],
        averageDuration: [
          { required: true, message: "平均工时不能为空", trigger: "change" },
          { type: "number", message: "平均工时必须为数字", trigger: "change" }
        ],
        repairSteps: [
          { required: true, message: "维修步骤不能为空", trigger: "change" }
        ],
        status: [
          { required: true, message: "状态不能为空", trigger: "change" }
        ]
      }
    };
  },

  methods: {    
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取维修项目数据
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            projectCode: "RP001",
            projectName: "交换机故障排除项目",
            applicableDevices: ["交换机"],
            repairLevel: "二级",
            averageDuration: 2.5,
            requiredParts: "网络测试仪、螺丝刀套装、备用电源模块",
            repairSteps: "1. 检查电源连接\n2. 测试网络接口\n3. 检查配置文件\n4. 更新固件版本\n5. 重置系统\n6. 更换故障部件（如需要）\n7. 测试系统功能",
            precautions: "1. 维修前必须断电\n2. 注意防静电措施\n3. 保存原有配置",
            status: "启用",
            createdBy: "admin",
            createdTime: "2024-03-18 10:00:00",
            modifiedBy: "admin",
            modifiedTime: "2024-03-18 11:55"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        projectCode: "",
        projectName: "",
        applicableDevices: [],
        repairLevel: undefined,
        averageDuration: 1.0,
        requiredParts: "",
        repairSteps: "",
        precautions: "",
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.projectName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null
          });
        }, 500);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
</style> 